
import fetch from 'isomorphic-unfetch';
import MasterPage from '../components/Master';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';
import Eventos from '../components/Eventos';

const Index = (props) => (
     <MasterPage>

     <div className=" row">
         <div  className=" col-12 ">
              <h2>Bitcoin  <span className="text-info">Price</span></h2>
              
              <Precio 
              precio={props.priceBitcoin}
              />
         </div>

         <div className="col-md-8">
              <h3>Bitcoin <span className=" alert text-warning ">News</span> </h3>
              <Noticias 
              noticias= {props.noticias}
              />
         </div>
         <div className="col-md-4">
              <h3>Bitcoin <span className=" alert text-success ">Events </span> </h3>
                <Eventos  eventos ={props.eventos} />
               
         </div>
          
     </div>
     </MasterPage>
);

Index.getInitialProps = async () => {
     const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');

     const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&from=2019-08-09&sortBy=publishedAt&apiKey=3f263a6832ca48328d7f58d238d9d9e4');

     const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=UK&token=7IMS25MFDYMVEM2AKSNE&locale=en_GB');

     const resPrice = await precio.json();
     const resNoticias = await noticias.json();
     const resEventos = await eventos.json();

     return {
          priceBitcoin: resPrice.data.quotes.USD,
          noticias : resNoticias.articles,
          eventos: resEventos.events


     }
}


export default Index;
